import PDFs from "../files/PDFs";

/** Generate link */
function Link(href: string, text: string, key = href) {
    return (
        <p key={key}><a href={href}>{text}</a></p>
    );
}

/** Make links */
function Links() {
    const years = Object.keys(PDFs).reverse();
    const abit = "https://abit.itmo.ru";
    const links = [
        { href: "/file/pages/82/rsosh_2022_bvi.pdf", name: " 2022,  " },
        { href: "/file/pages/82/100ballov_2022.pdf", name: " 2022,  100 " },
        { href: "/file/pages/82/vosh_2022.pdf", name: " (  )" },
        { href: "/page/101/", name: " ()" },
        { href: "/page/102/", name: " ()" }
    ];

    return (
        <div className="links">
            <div className="side left">
                {years.map(year => Link(PDFs[year], `  ${year}`, year))}
            </div>
            <div className="side right">
                {links.map(({ href, name }) => Link(abit + href, name))}
            </div>
        </div>
    );
}

export default Links;