import { bvi, sto, ia, itin, wtf, confPoints } from "./constants";
import { olympSubjBy, ids, fromWLS } from "./constants";
import { setPointsColor } from "./colors";
import { yesconf, nonconf } from "./diploma";
import { IOlymp } from "./search";
import { IEGE } from "../hooks/ege.hook";

/** Check current olymp status by params */
function checkBVI(EGE: IEGE, stream: string, { lvl, dip, subj, name }: IOlymp) {
    let status: string;

    const ch75 = checkConf(EGE, stream, subj);
    const bvi_wtf = (local_lvl: number) => (lvl === local_lvl) ? bvi : ia;
    const bvi_sto = (local_lvl: number) => (lvl <= local_lvl) ? bvi : sto;
    const lvldip1 = (local_lvl: number) => (lvl <= local_lvl && dip === 1) ? bvi : sto;

    if (subj === ' ') {
        return (ch75 === yesconf) ? sto : wtf;
    }
    if (ch75 === wtf) {
        // break now if olymp profile is false
        return wtf;
    }

    switch (stream) {
        case '01.03.02':
            switch (subj) {
                case '':
                    switch (name) {
                        case '  ""':
                        case '   ':
                        case '   "Innopolis Open"':
                            status = bvi_sto(2);
                            break;
                        case '  ':
                        case '     ':
                        case '    ""':
                        case '  -  ':
                        case '  ':
                        case '  ':
                        case '    ':
                        case '     " "':
                        case '   " "':
                        case '-   ':
                            status = bvi_sto(3);
                            break;
                        default:
                            status = sto;
                    }
                    break;
                case '  ':
                    status = lvldip1(2);
                    break;
                case '':
                    switch (name) {
                        case '  "  "':
                            status = lvldip1(2);
                            break;
                        case '-     ':
                            status = lvldip1(3);
                            break;
                        default:
                            status = sto;
                    }
                    break;
                case '':
                    switch (name) {
                        // case '   " "':
                        case '   " "':
                        case '  ':
                        case '  ""':
                        case '  -  ':
                        case '-  ':
                        case ' ':
                            status = bvi_sto(1);
                            break;
                        case '  "  !"':
                        case '  "  !"':
                            status = lvldip1(1);;
                            break;
                        case '   ':
                        case '   . ..':
                        case '       ':
                        case '    ':
                        case '    " " / " "':
                        case ' ':
                        case '  ""':
                        case '   ':
                        case ' -   ""':
                            // case ' ""':
                            status = lvldip1(2);
                            break;
                        // case ' ':
                        case '  .. ':
                            status = bvi_sto(2);
                            break;
                        case '  ':
                        case '     ""':
                            // case '      ""':
                            // case '   "Innopolis Open"':
                            status = lvldip1(3);
                            break;
                        default:
                            status = sto;
                    }
                    break;
                case '    ':
                    status = (name === "  ") ? lvldip1(2) : sto;
                    break;
                // case ' ':
                // case '   ':
                // case ' ':
                case ' ':
                case ' ':
                case '   ':
                    status = sto;
                    break;
                default:
                    status = wtf;
            }
            break;
        case '09.03.02':
            switch (subj) {
                case '':
                    status = bvi_sto(2);
                    break;
                case '':
                case ' ':
                    status = lvldip1(3);
                    break;
                case '':
                    status = (lvl === 3) ? (name === '  ') ? bvi : sto : bvi;
                    break;
                case '    ':
                    status = bvi_sto(2);
                    break;
                // case '   ':
                // case ' ':
                // case ' ':
                case ' ':
                case '   ':
                case '  ':
                    status = sto;
                    break;
                default:
                    status = wtf;
            }
            break;
        case '45.03.04':
            switch (subj) {
                case '':
                case '':
                case '':
                case '  ':
                case '   ':
                case ' ':
                case '    ':
                case ' ':
                case ' ':
                case ' ':
                case ' ':
                case ' ':
                case '':
                    status = bvi;
                    break;
                // case ' ':
                //     status = bvi_wtf(1);
                //     break;
                default:
                    status = wtf;
            }
            break;
        case '09.03.01':
        case '09.03.03':
        case '09.03.04':
        case '10.03.01':
        case '11.03.02':
        case '11.03.03':
        case '12.03.01':
        case '13.03.02':
        case '15.03.04':
        case '15.03.06':
        case '23.03.03':
        case '24.03.02':
        case '27.03.04':
            switch (subj) {
                // case ' ':
                //     status = bvi_wtf(1);
                //     break;
                case '':
                case '':
                case '':
                case '   ':
                case ' ':
                case '  ':
                case '  ':
                case '  ':
                case '  ':
                case ' ':
                case ' ':
                case '     ':
                case '  ':
                case '   ':
                case '  ':
                case ' ':
                case '  ':
                case ' ':
                    // case ' ':
                    // case '':
                    // case '':
                    // case '   ':
                    // case ' ':
                    // case ' ':
                    // case '   ':
                    status = bvi;
                    break;
                // case '     ':
                // case '   ':
                // case ' ':
                case ' -':
                case ' ':
                case '    ':
                    status = bvi_sto(2);
                    break;
                case '  ':
                    status = bvi_wtf(2);
                    break;
                default:
                    status = wtf;
            }
            break;
        case '44.03.04':
            switch (subj) {
                // case ' ':
                //     status = bvi_wtf(1);
                //     break;
                case '':
                case '':
                case '':
                case '  ':
                case '   ':
                case '   ':
                case '   ':
                case '     ':
                case '':
                case ', ':
                case ' ':
                case '      ':
                case '':
                case ' , , ,    ':
                    // case ' ':
                    // case '':
                    // case '   ':
                    status = bvi;
                    break;
                // case ', , , ':
                //     status = bvi_wtf(1);
                //     break;
                // case '':
                // case ',     ':
                // case ' -':
                // case '   ':
                // case '    ':
                //     status = bvi_wtf(2);
                //     break;
                default:
                    status = wtf;
            }
            break;
        case '12.03.02':
        case '12.03.03':
        case '12.03.05':
        case '13.03.01':
        case '16.03.03':
            switch (subj) {
                // case ' ':
                //     status = bvi_wtf(1);
                //     break;
                // case '   ':
                // case '     ':
                // case '  ':
                // case '':
                // case '     ':
                // case ' ':
                case '':
                case '':
                case '  ':
                case ' ':
                case ' ':
                case ' ':
                case ' ':
                case ' ':
                case '':
                case '    ':
                case '':
                case '  ':
                case '  ':
                case '  (, , )':
                case '':
                    status = bvi;
                    break;
                // case '':
                // case '   ':
                // case '':
                case '  ':
                    status = bvi_wtf(2);
                    break;
                default:
                    status = wtf;
            }
            break;
        case '16.03.01':
            switch (subj) {
                case '':
                case '':
                case '    ':
                case '':
                case ' ':
                case ' ':
                case ' ':
                case ' ':
                case '  ':
                case '':
                    // case '     ':
                    // case ' ':
                    // case '  ':
                    // case '     ':
                    // case '  ':
                    // case ' ':
                    // case '  (, , )':
                    // case '':
                    // case '':
                    status = bvi;
                    break;
                // case '  ':
                // case '':
                // case '   ':
                //     status = bvi_wtf(2);
                //     break;
                default:
                    status = wtf;
            }
            break;
        case '05.03.06':
        case '12.03.04':
        case '18.03.01':
        case '18.03.02':
        case '19.03.01':
            switch (subj) {
                case '':
                case '':
                case ' ':
                case '':
                case '  ':
                case '  : ':
                case '':
                case '  ':
                case '  ':
                case '':
                case ' ':
                case '':
                    status = bvi;
                    break;
                default:
                    status = wtf;
            }
            break;
        case '27.03.05':
        case '38.03.05':
            switch (subj) {
                // case ' ':
                //     status = bvi_wtf(1);
                //     break;
                case '':
                case '':
                case ' ':
                case ' ':
                case ' ':
                case ' ':
                case '':
                case '':
                case '':
                case '':
                case '   ':
                case ' ':
                case ' ':
                case ' -':
                case '':
                    status = bvi;
                    break;
                default:
                    status = wtf;
            }
            break;
        default:
            status = wtf;
    }

    if (status !== wtf) {
        status += ch75;
    }

    return status;
}

/** Check olymp confirmation by subject profile points */
function checkConfNum(EGE: IEGE, currSubj: string, multiSubjs?: { [key: string]: string[]; }, stream?: string) {
    const currEge = EGE[currSubj];
    const proof = (stream) ? multiSubjs![currSubj].includes(stream) : true;
    const conf = currEge >= confPoints;
    if (!fromWLS) {
        setPointsColor(ids[currSubj], proof, Boolean(currEge) === false, conf);
    }
    return proof && conf;
}

/** Check olymp confirmation by stream and subject profile */
function checkConf(EGE: IEGE, stream: string, olympProfile: string) {
    let stat = false;
    const confSubj = olympSubjBy[olympProfile];
    switch (typeof confSubj) {
        case 'undefined':
            return itin;
        case 'boolean':
            return wtf;
        case 'object':
            for (const i of Object.keys(confSubj)) {
                stat = checkConfNum(EGE, i, confSubj, stream);
                if (stat) {
                    break;
                }
            }
            break;
        case 'string':
            stat = checkConfNum(EGE, confSubj);
            break;
        default:
            alert(" , !");
    }
    return (stat) ? yesconf : nonconf;
}

export { checkBVI };